body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #FFF8E7;
  color: #3D3D3D;
  line-height: 1.6;
}
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}
header {
  position: sticky;
  top: 0;
  background-color: #FFF8E7;
  border-bottom: 1px solid #D4A017;
  z-index: 1000;
}
.logo {
  width: auto;
  height: 48px;
  margin-right: 10px;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  ul {
    list-style-type: none;
    display: flex;
    gap: 1.5rem;
    margin: 0;
    padding: 0;
  }
  a {
    text-decoration: none;
    color: #3D3D3D;
    transition: color 0.3s;
    &:hover {
      color: #D4A017;
    }
  }
}

.text-center {
  text-align: center;
}

.menu-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}
main {
  padding: 3rem 0;
}
h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.cta-buttons {
  margin-top: 2rem;
}
.button {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
  margin: 0.5rem;
  vertical-align: middle;
}
.primary {
  background-color: #D4A017;
  color: white;
}
.primary:hover {
  background-color: #B8860B;
}
.how-it-works {
  background-color: #FFF2CC;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 3rem;
  text-align: left;
}
.how-it-works h2 {
  text-align: center;
  margin-bottom: 1.5rem;
}
.how-it-works ul {
  padding-left: 1.5rem;
}
.how-it-works li {
  margin-bottom: 0.5rem;
}
footer {
  border-top: 1px solid #D4A017;
  text-align: center;
  padding: 1rem 0;
  margin-top: 3rem;
}

@media (max-width: 768px) {
  nav {
    flex-direction: column;
    align-items: flex-start;
  }
  nav ul {
    flex-direction: column;
    width: 100%;
    display: none;
  }
  nav ul.show {
    display: flex;
  }
  nav li {
    margin: 0.5rem 0;
  }
  .menu-toggle {
    display: block;
    position: absolute;
    top: 1rem;
    right: 1rem;
  }
  h1 {
    font-size: 2rem;
  }
  .how-it-works {
    padding: 1rem;
  }
}

.text-newbie {
  color: gray;
}
.text-pupil {
  color: green;
}
.text-specialist {
  color: #03A89E;
}

.blog {
  h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: #D4A017;
  }
  h2 {
    font-size: 1.8rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
    color: #B8860B;
  }
  p {
    margin-bottom: 1rem;
  }
  img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin: 1rem 0;
  }
  pre {
    background-color: #f5f2f0;
    border-left: 4px solid #D4A017;
    padding: 1rem;
    overflow-x: auto;
    font-family: 'Courier New', Courier, monospace;
  }
  code {
    font-family: 'Courier New', Courier, monospace;
    background-color: #f5f2f0;
    padding: 2px 4px;
    border-radius: 4px;
  }
}